import * as React from 'react';
import {Button, FileInput, FileInputSelectors, Form, Section} from 'react-vapor';

const inputId = 'file-input-example';

export const FileInputExamples: React.FunctionComponent = () => {
    function logTheSelectedFile() {
        // tslint:disable-next-line
        console.log(FileInputSelectors.getFile(inputId));
    }

    return (
        <Form>
            <Section>
                <FileInput id={inputId} accept=".csv,.txt" placeholder="Choose a file..." />
                <Button name="log file" primary onClick={logTheSelectedFile} />
            </Section>
        </Form>
    );
};
